<template>
  <div id="tree-map">
    <svg></svg>
  </div>
</template>

<script>
  import TreeMap from '../../common/js/TreeMap'
  import Bar from '../../common/js/Bar'
    export default {
      data() {
        return {
          data: [['小米', 40.7], ['小5米', 48.7],['小1米', 74.7],
            ['华为',52.1], ['金立', 49.7], ['联想', 35.6], ['1联想', 39.6],
            ['联2想', 45.6], ['兴联', 70]],
         /* data:{
            'name': '支出',
            'children':[
              {
                'name':'小米',
                'value':40.7
              },
              {
                'name':'小5米',
                'value':48.7
              },
              {
                'name':'小1米',
                'value':74.7
              },
              {
                'name':'华为',
                'value':52.1
              },
              {
                'name':'金立',
                'value':49.7
              },
              {
                'name':'联想',
                'value':35.6
              },
              {
                'name':'1联想',
                'value':39.6
              },
              {
                'name':'联2想',
                'value':45.6
              },
              {
                'name':'兴联',
                'value':70
              }
            ]
          },*/
          config: {
            width: 400,
            height: 200,
            padding: {
              left: 30,
              right: 30,
              top: 20,
              bottom: 20
            }
          }
        }
      },
      mounted() {
        this.$nextTick(()=>{
          let width = document.getElementById('tree-map').clientWidth
          this.config.width = width
          this.config.height = width*0.55
          let bar = new Bar('#tree-map',this.config)
          bar.draw(this.data)
        })

      }
    }
</script>

<style scoped lang="less">
#area{
  width: 100%;
}

</style>



